body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app * {
  /* don't allow the children of the scrollable element to be selected as an anchor node */
  overflow-anchor: none;
  /* width: 100%; */
}

#anchor {
  /* allow the final child to be selected as an anchor node */
  overflow-anchor: auto;

  /* anchor nodes are required to have non-zero area */
  height: 1px;
}

ul {
  padding: 0;
}

li {
  list-style: none;
  margin-bottom: 20px;
}

.evaluatorSpace {
  margin-top: 5em;
  margin-left: 5vw;
  width: 90vw;
}

.evaluationHeader {
  height: 1em;
  background-color: #0091ea;
  color: white;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  padding: 1em;
}

.evaluationHeader i {
  margin-right: 1em;
  cursor: pointer;

  /* font-size: 1em; */

  /* display: inline */
}

.evaluationHeader p {
  /* margin-left: 1px; */

  display: inline
}

.prompt {
  width: 95%;
  font-size: 3em;
  border: none;
  overflow: auto;
  outline: none;
  resize: none;
  border-style: none;
}

.editor {
  position: relative;
  /* bottom: 0; */
  margin-left: 5vw;
  width: 90vw;
  /* margin-top : 5vh; */
  /* border-bottom: 2px solid gray; */
  /* padding: 10px; */
  border: 1px solid gray;
  border-radius: 5px;
}

#editorEnter {
  position: absolute;
  right: 0;
  bottom: 2px;

  /* font-size: 1em; */
  color: #1976d2;
  /* border-radius: 3px; */
  /* border-style: none; */
	/* border-bottom: 2px solid navy; */
  /* margin-left: 5px; */
  /* margin-right: 5px;   */
  cursor: pointer;
  /* outline: none; */
}
.evaluator {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;

  border: 1px solid black;
  border-top: none;
  /* padding-left: 1em; */
  padding-top: 5px;
}

.inactiveStep {
  color: gray;
}
#controls {
  margin-bottom: 10px;
}

.enabled {
  font-size: 1.5em;
  /* background: none; */
  /* border-style: none; */
	/* border-bottom: 2px solid navy; */
  /* margin-left: 5px; */
  /* margin-right: 5px;   */
  cursor: pointer;
  /* padding-left: 5px; */
}

.disabled {
  font-size: 1.5em;
  cursor: auto;
  color: grey;
}

#controls i {
  padding-left: 5px;
  padding-right: 5px;
}
.step {
  font-size: 2em;
  word-wrap: anywhere;
  padding-left: 10px;
}

.left.redex {
  background-color: #69f0ae;
  border-radius: 5px;
}

.right.redex {
  background-color: #ff80ab;
  border-radius: 5px;
}

.activeStep .macro, .activeStep .lambda, .activeStep .argument, .activeStep .churchnumeral {
  cursor: pointer;
}


.macro.redex {
  background-color: #80d8ff;
  border-radius: 5px;
}

.churchnumeral.redex {
  background-color: #82b1ff;
  border-radius: 5px;
}
#topbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 3em;
  background-color: #1976d2;
  color: white;
  padding-top: 1em;
  padding-left: 5vw;
  /* font-size: 2em; */

  /* border: 1px solid black; */
}

#input {
	width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	z-index: -1;
}

.icon {
  color: white;
  margin-right: 20px;
}

#input + label {
  position: relative;
  top: -5px;
  font-size: 1.25em;
  /* font-weight: 700; */
  color: white;
  background-color: #42a5f5;
  display: inline-block;
  padding: 5px;
  border-radius: 5px;
}

#input:focus + label,
#input + label:hover {
  background-color: #1565c0;
}

#input + label {
	cursor: pointer; /* "hand" cursor */
}

#input:focus + label {
	outline: 1px dotted white;
	outline: -webkit-focus-ring-color auto 5px;
}
