#topbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 3em;
  background-color: #1976d2;
  color: white;
  padding-top: 1em;
  padding-left: 5vw;
  /* font-size: 2em; */

  /* border: 1px solid black; */
}

#input {
	width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	z-index: -1;
}

.icon {
  color: white;
  margin-right: 20px;
}

#input + label {
  position: relative;
  top: -5px;
  font-size: 1.25em;
  /* font-weight: 700; */
  color: white;
  background-color: #42a5f5;
  display: inline-block;
  padding: 5px;
  border-radius: 5px;
}

#input:focus + label,
#input + label:hover {
  background-color: #1565c0;
}

#input + label {
	cursor: pointer; /* "hand" cursor */
}

#input:focus + label {
	outline: 1px dotted white;
	outline: -webkit-focus-ring-color auto 5px;
}